  getAllRequests(/* page: number */) {
    let data: IsearchModel = {
      // page: page,
      EduCompId: this.EduCompId,
      mobile: /* this.myFormCtrl.mobile.value?.trim() */ null,
      code: /* this.myFormCtrl.code.value?.trim().toLowerCase() */ null,
      account_email:
        /* this.myFormCtrl.email.value?.trim().toLowerCase() */ null,
      approvement: /* this.myFormCtrl.approve.value */ null,
      courseId: /* this.myFormCtrl.course.value */ null,
    };

    // this.spinner.show();
    this.courseAndTeachingRequestService
      .getAllTeachingRequests(data)
      .subscribe((res) => {
        if (res) {
          // this.dataSource = new MatTableDataSource(res.Requests);
          this.teachingRequests = res.Requests;
          // this.totalItems = res.countItems;
          // this.startSortingTable();
        }
        this.isLoaded = true;
        // this.spinner.hide();
      });
  }

  approveTeachigRequest(request: RequestData, status: boolean) {
    // console.log(request, status);
    this.spinner.show();
    this.courseAndTeachingRequestService
      .ApproveTeachingRequest(request.id, status)
      .subscribe((res: any) => {
        if (res.returnValue == 1) {
          let i = this.teachingRequests.findIndex((ele) => ele == request);
          this.teachingRequests[i].status = status;
          // let i = this.dataSource.data.findIndex((ele) => ele == request);
          // this.dataSource.data[i].status = status;
          this.tostar.success("General.UpdateSuccessfully");
        } else this.tostar.error(res?.returnString);
        if (res.courseId && status == true)
          this.router.navigate(
            [`dashboard/setup/course/edit-course/${res.courseId}/1`],
            {
              queryParams: {
                functionId: this.functionId,
              },
            }
          );
        this.spinner.hide();
      });
  }

