import {Component, Inject, OnInit} from "@angular/core";
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {TranslateService} from "@ngx-translate/core";
import {GiftsService} from "app/admin/services/Admin/gifts.service";
import {AuthService} from "app/shared/services/auth/auth.service";
import {NgxSpinnerService} from "ngx-spinner";
import {ToastrService} from "ngx-toastr";
import {MAT_DIALOG_DATA, MatDialogRef} from "@angular/material/dialog";
import {giftCategoryViewModel} from "app/admin/models/admin/gifts";

@Component({
  selector: "app-gifts-category-add",
  templateUrl: "./gifts-category-add.component.html",
  styleUrls: ["./gifts-category-add.component.scss"],
})
export class GiftsCategoryAddComponent implements OnInit {
  giftcategoryId: number = 0;
  myForm!: FormGroup;
  submitted = false;
  currentLang: string;
  EduCompId: any;

  constructor(
    private fb: FormBuilder,
    private authserv: AuthService,
    private giftsService: GiftsService,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    private msg: ToastrService,
    public dialogRef: MatDialogRef<GiftsCategoryAddComponent>,
    @Inject(MAT_DIALOG_DATA) public gift: giftCategoryViewModel
  ) {
    this.giftcategoryId = gift?.id ?? 0;
  }

  get engNameCtrl() {
    return this.myForm.get("englishName");
  }

  get arbNameCtrl() {
    return this.myForm.get("arabicName");
  }

  get IdCtrl() {
    return this.myForm.get("id");
  }

  ngOnInit() {
    this.EduCompId = this.authserv.getEduCompId();
    this.myForm = this.fb.group({
      id: [null],
      englishName: [null, Validators.required],
      arabicName: [null, Validators.required],
      EduCompId: [this.EduCompId],
    });
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });

    if (this.giftcategoryId > 0) {
      this.IdCtrl.setValue(this.gift.id);
      this.arbNameCtrl.setValue(this.gift.arabicName);
      this.engNameCtrl.setValue(this.gift.englishName);
    }
  }

  ngAfterViewInit() {
    this.EduCompId = this.authserv.getEduCompId();
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });
  }

  submit() {
    this.submitted = true;
    let valid = this.myForm.valid;
    if (valid) {
      this.spinner.show();
      this.giftsService
        .addEditCategory(this.myForm.value)
        .subscribe((res: any) => {
          if (res.returnValue == 1) {
            this.dialogRef.close(true);
            if (this.IdCtrl.value == 0) {
              if (this.currentLang == "ar") {
                this.msg.success("  ");
              } else {
                this.msg.success("Gift Category Added ");
              }
            } else {
              if (this.currentLang == "ar") {
                this.msg.success("  ");
              } else {
                this.msg.success("Gift Category Updated ");
              }
            }
          } else {
            if (this.currentLang == "ar") {
              this.msg.success("    ");
            } else {
              this.msg.success("This name is already exist");
            }
          }

          this.spinner.hide();
        });
    }
  }
}
