  getAllQuestionsByHeadId(status: number = 0) {
    this.spinner.show();
    this.examService
      .getAllQuestionsByHeadId<IQuestionForGridModel[]>(this.examGroupHeaderId)
      .subscribe((response) => {
        if (response) {
          // console.log("getAllQuestionsByHeadId", response);
          this.questionsInGrid = [];
          response.map(
            (ques) =>
              (ques.correctIndex = ques.AnswersIds.findIndex(
                (answerId) => answerId == ques.CorrectAnswerId
              ))
          );
          this.questionsInGrid = response;
          if (this.questionsInGrid.length == 0 /* || status == 0 */) {
            this.pushNewQuestion();
          } else if (this.questionsInGrid.length > 0 && status == 0) {
            this.questionId = this.questionsInGrid[0].ExamQuestId;
            this.getQuestionForEditByID(this.questionId);
          }
          this.handleStatusAfterSubmit(status);
          this.spinner.hide();
        }
      });
  }

