  addMcqQuestion(toAddEdit: IAddEditQuestionMcqModel, status: number) {
    // console.log('on add ', toAddEdit);
    if (this.myForm.valid) {
      this.spinner.show();
      this.examService
        .addQuestion<IAddedTextExamResponse>(toAddEdit)
        .subscribe((response: any) => {
          // console.log('respooooooonse', response);

          if (response.returnValue == 200) {
            this.getAllQuestionsByHeadId(status);

            this.FormCtrl.mainQuestion?.setValue(null);
            this.FormCtrl.questionMark?.setValue(null);
            this.FormCtrl.questionTypeId?.setValue(null);
            this.FormCtrl.questionImage?.setValue(null);
            this.FormCtrl.exam_question_text?.setValue(null);
            this.FormCtrl.questionPerfectAnswer?.setValue(null);
            this.FormCtrl.perfect_answer_attach?.setValue(null);
            // this.FormCtrl.unitId?.setValue(null);
            // this.FormCtrl.lessonId?.setValue(null);

            this.clearForm();
            this.toastr.success("General.AddSuccessfully");
          } else {
            this.toastr.error(response.returnString);
          }
          this.spinner.hide();
        });
    }
  }

  editMcqQuestion(
    toAddEdit: IAddEditQuestionMcqModel,
    status: number,
    quesIdToSelect: number
  ) {
    //console.log("editMcqQuestion", toAddEdit);
    if (this.myForm.valid) {
      this.spinner.show();
      this.examService
        .editQuestion<IQuestionDetailsForEditModel>(toAddEdit)
        .subscribe((response) => {
          //  console.log("editMcqQuestion", response);
          if (response) {
            this.questionId = quesIdToSelect;
            this.getAllQuestionsByHeadId(status);
            this.FormCtrl.mainQuestion?.setValue(null);
            this.FormCtrl.questionMark?.setValue(null);
            this.FormCtrl.questionTypeId?.setValue(null);
            this.FormCtrl.questionImage?.setValue(null);
            this.FormCtrl.exam_question_text?.setValue(null);
            this.FormCtrl.questionPerfectAnswer?.setValue(null);
            this.FormCtrl.perfect_answer_attach?.setValue(null);
            this.oneIsCorrect = false;
            this.twoIsCorrect = false;
            this.threeIsCorrect = false;
            this.fourIsCorrect = false;
            // this.FormCtrl.unitId?.setValue(null);
            // this.FormCtrl.lessonId?.setValue(null);

            this.clearForm();
            this.toastr.success("General.UpdateSuccessfully");
          }
          this.spinner.hide();
        });
    }
  }

  handleStatusAfterSubmit(status: number) {
    // if there is non saved question save it
    if (this.notSavedQuestionData && [1, 2, 6, 7].includes(status)) {
      let newData: IQuestionForGridModel = {
        ExamQuestId: 0,
        mark: this.notSavedQuestionData.questionMark,
        Text: this.notSavedQuestionData.exam_question_text,
        ChoiceCounts: this.noOfInsertedAnswers(false),
        Image: this.notSavedQuestionData.questionImage,
        correctIndex: this.getCorrectAnswerIndexFromForm(false),
      };
      this.questionsInGrid.push(newData);
    }

    if (status == 1) this.goToPrevQuestion();
    else if (status == 2) this.goToNextQuestion();
    else if (status == 3) {
      this.pushNewQuestion();
      this.goToNotSavedQuestion(true);
    } else if (status == 4) {
      this.pushNewQuestion();
      this.currentQuestionToEdit = null;
      this.questionId = 0;
      this.clearForm();
    } else if (status == 5)
      this.router.navigate(
        [`/dashboard/setup/course/course-structure/${this.subjectId}`],
        {
          queryParams: {
            functionId: this.functionId,
          },
        }
      );
    else if (status == 6) {
      this.myForm.patchValue(this.notSavedQuestionData);
      this.displayQuestionImageInEditor();
      this.currentQuestionToEdit = null;
    } else if (status == 7) this.getQuestionForEditByID(this.questionId);
  }

