import {CourseService} from "app/admin/services/Admin/setup/course.service";
import {Component, OnInit, TemplateRef, ViewChild} from "@angular/core";
import {MatDialog} from "@angular/material/dialog";
import {ActivatedRoute, Router} from "@angular/router";
import {AddUnitComponent} from "app/admin/pages/setup/courses/course-structure/add-unit/add-unit.component";
import {DeleteDialogComponent} from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import {IRowFunctionVM} from "app/shared/models/general/general";
import {AuthService} from "app/shared/services/auth/auth.service";
import {MessagesService} from "app/shared/services/messages.service";
import {NgxSpinnerService} from "ngx-spinner";
import {
  AddMaterialMainDataComponent
} from "app/admin/pages/educational-setup/EducationalSubject/add-materials/add-main-data/add-main-data.component";
import {unit} from "app/admin/models/admin/educations";
import {SubjectsService} from "app/admin/services/Admin/subjects.service";
import {AddExamComponent} from "app/admin/pages/exams-setup/exams/add-exam/add-exam.component";
import {AddUnitAttachmentComponent} from "../add-unit-attachment/add-unit-attachment.component";
import {ExamsService} from "app/admin/services/Admin/exams.service";
import {SubjectMaterialsService} from "app/admin/services/Admin/subjectMaterials.service";
import {Observable} from "rxjs";
import {ICourseGetVM, IUnitDataGeneralVM} from "app/admin/models/admin/setup/course";
import {AppStrings} from "app/shared/data/app-strings";
import {LanguageService} from "app/shared/services/language.service";
import {SvgManager} from "app/shared/data/svg-manager";
import {AppAssets} from "app/shared/data/app-assets";
import {
  ExamsIntegrationComponent
} from "app/admin/pages/setup/courses/course-structure/exams-integration/exams-integration.component";

@Component({
  selector: "app-structure-index",
  templateUrl: "./structure-index.component.html",
  styleUrls: ["./structure-index.component.scss"],
})
export class StructureIndexComponent implements OnInit {
  units: unit[] = [];
  // contain exam , lectures , attachments
  unitData: IUnitDataGeneralVM[] = [];
  subjectId: number;
  selectedUnitId: number;
  selectedUnitName: string;
  selectedUnitIndex: number = 0;

  EduCompId: number;
  // branchId: number;
  functionId: number;
  rowFunctions: IRowFunctionVM[];
  svgManager = new SvgManager();
  assets = new AppAssets();
  selectedCreationItem: string;
  @ViewChild('dialogTemplate') dialogTemplate!: TemplateRef<any>;
  isLoaded: boolean = false;
  isCourseLoaded: boolean = false;
  course: ICourseGetVM;

  constructor(
    private spinner: NgxSpinnerService,
    private route: ActivatedRoute,
    private subjectsService: SubjectsService,
    private courseService: CourseService,
    private examService: ExamsService,
    private SubjectMaterialsService: SubjectMaterialsService,
    private router: Router,
    public authserv: AuthService,
    private msg: MessagesService,
    private langService: LanguageService,
    private dialog: MatDialog,
    public appStrings: AppStrings
  ) {
    route.paramMap.subscribe((params) => {
      this.subjectId = Number(params.get("courseId"));
      this.courseService.getCourseById(this.subjectId).subscribe((res: ICourseGetVM) => {
        this.course = res;
        this.isCourseLoaded = true;
      })
    });

    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res) {
              this.rowFunctions = res;
            }
          });
      }
    });
  }

  ngOnInit(): void {
    this.getUnits();
  }

  //Get Subject Structre
  getUnits() {
    this.spinner.show();
    this.subjectsService.getUnits(this.subjectId).subscribe((res: unit[]) => {
      if (res.length > 0) {
        this.units = res;
        this.changeSelectedUnit(res[0], 0);
      }
      this.spinner.hide();
    });
  }

  // get lectures & exams & attachments of selected unit
  getSelectedUnitData() {
    this.isLoaded = false;
    this.courseService.getAllUnitData(this.selectedUnitId).subscribe((res) => {
      if (res) {
        this.isLoaded = true;
        this.unitData = res.model;
        this.sortUnitDataByOrder();
        // console.log("selected unit data", this.unitData);
      }
    });
  }

  changeSelectedUnit(unit: unit, index: number) {
    this.selectedUnitIndex = index > this.units.length - 1 ? 0 : index;
    if (!unit) unit = this.units[this.selectedUnitIndex];

    this.unitData = [];
    this.selectedUnitId = unit.id;
    this.selectedUnitName = this.langService.isArabic
      ? unit.unit_ar_name
      : unit.unit_en_name;
    this.getSelectedUnitData();
  }

  openAddEditUnitDialog(unitId: number): void {
    this.dialog
      .open(AddUnitComponent, {
        data: {
          unitId: unitId > 0 ? unitId : 0,
          subjectId: this.subjectId,
        },
        width: "500px",
        panelClass: 'dialog-radius'
        // minHeight: "60vh",
      })
      .afterClosed()
      .subscribe((unit: unit) => {
        if (unit?.id > 0) {
          let i = this.units.findIndex((e) => e.id == unit.id);
          i < 0 ? this.units.push(unit) : (this.units[i] = unit);
          // if name of selected unit changed
          if (unit?.id == this.selectedUnitId || this.units.length == 1) {
            this.selectedUnitName = this.langService.isArabic
              ? unit.unit_ar_name
              : unit.unit_en_name;
            // if the first unit to add
            this.selectedUnitId = unit.id;
          }
        }
      });
  }

  openDeleteUnitDialog(unit: unit): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: this.langService.isArabic
            ? unit.unit_ar_name
            : unit.unit_en_name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteUnit(unit);
      });
  }

  deleteUnit(unit: unit) {
    this.spinner.show();
    this.subjectsService.deleteUnit(unit.id).subscribe((res: any) => {
      if (res.returnValue == 1) {
        let i = this.units.indexOf(unit);
        this.units.splice(i, 1);
        this.msg.success("General.DeleteSuccessfully");
        if (unit.id == this.selectedUnitId)
          this.changeSelectedUnit(null, this.selectedUnitIndex);
      } else this.msg.error(res?.returnString);
      this.spinner.hide();
    });
  }

  /////////////// lecture | exam | attach

  editUnitData(data: IUnitDataGeneralVM) {
    if (data.type == this.appStrings.stageLecture)
      this.openAddEditLectureDialog(data.id);
    else if (data.type == this.appStrings.stageExam)
      this.openAddEditExamDialog(data.id);
    else if (data.type == this.appStrings.stageAttach)
      this.openAddEditAttachmentDialog(data.id);
  }

  openAddEditLectureDialog(lectureId: number): void {
    this.dialog
      .open(AddMaterialMainDataComponent, {
        data: {
          subjectId: this.subjectId,
          unitId: this.selectedUnitId,
          // material id > edit
          materialId: lectureId > 0 ? lectureId : null,
        },
        minHeight: "70vh",
        width: "500px"
      })
      .afterClosed()
      .subscribe((lecture: IUnitDataGeneralVM) => {
        this.getSelectedUnitData();
        //if (lecture) this.afterCloseUnitDataDialogs(lecture);
      });
  }

  openAddEditExamDialog(examId: number): void {
    this.dialog
      .open(AddExamComponent, {
        data: {
          subjectId: this.subjectId,
          unitId: this.selectedUnitId,
          type: 3,
          // edit
          examId: examId > 0 ? examId : 0,
        },
        // minWidth: "60vw",
        maxHeight: "99vh",
        // panelClass: "no-padding-dialog",
        width: "700px",
        autoFocus: false,
      })
      .afterClosed()
      .subscribe((exam: IUnitDataGeneralVM) => {
        this.getSelectedUnitData();
        // if (exam) this.afterCloseUnitDataDialogs(exam);
      });
  }

  openAddEditAttachmentDialog(attachmentId: number): void {
    this.dialog
      .open(AddUnitAttachmentComponent, {
        data: {
          subject_id: this.subjectId,
          unitId: this.selectedUnitId,
          attachmentId: attachmentId,
        },
        maxHeight: "99vh",
        width: "500px",
        autoFocus: false,
      })
      .afterClosed()
      .subscribe((attachment: IUnitDataGeneralVM) => {
        this.getSelectedUnitData();
        // if (attachment) this.afterCloseUnitDataDialogs(attachment);
      });
  }

  afterCloseUnitDataDialogs(data: IUnitDataGeneralVM) {
    let i = this.unitData.findIndex((e) => e.id == data.id);
    i < 0 ? this.unitData.push(data) : (this.unitData[i] = data);
    this.sortUnitDataByOrder();
  }

  openDeleteUnitDataDialog(data: IUnitDataGeneralVM): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: this.langService.isEnglish
            ? data.ar_name ?? data.en_name
            : data.en_name ?? data.ar_name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteUnitData(data);
      });
  }

  deleteUnitData(data: IUnitDataGeneralVM) {
    let api: Observable<Object>;
    if (data.type == this.appStrings.stageLecture)
      api = this.SubjectMaterialsService.deleteMaterial(data.id);
    else if (data.type == this.appStrings.stageExam)
      api = this.examService.deleteExam(data.id);
    else if (data.type == this.appStrings.stageAttach)
      api = this.courseService.deleteUnitAttachment(data.id);

    this.spinner.show();
    api.subscribe((response: any) => {
      // if (response.data == -9) this.msg.error(response?.returnString);
      if (response.data == 1 || response.returnValue == 1) {
        let deletedIndex = this.unitData.findIndex((ele) => ele == data);
        this.unitData.splice(deletedIndex, 1);
        this.msg.success("General.DeleteSuccessfully");
      } else this.msg.success(response?.returnString ?? "General.error");
      this.spinner.hide();
    });
  }

  sortUnitDataByOrder() {
    this.unitData = this.unitData.sort((a, b) => a.order - b.order);
  }

  track(index: number, el: any): number {
    return el.id;
  }

  getFileIcon(type): string {
    if (type == this.appStrings.stageLecture) return this.svgManager.folder;
    if (type == this.appStrings.stageExam) return this.svgManager.exam_content;
    if (type == this.appStrings.stageAttach) return this.svgManager.pdf;
  }

  openCreationDialog(): void {
    this.selectedCreationItem = null;
    this.dialog.open(this.dialogTemplate, {
      panelClass: 'dialog-radius'
    })
      .afterClosed()
      .subscribe(data => {
      });
  }


  next() {
    if (this.selectedCreationItem) this.dialog.closeAll();

    if (this.selectedCreationItem == this.appStrings.stageLecture)
      this.openAddEditLectureDialog(null);

    else if (this.selectedCreationItem == this.appStrings.stageExam)
      this.openAddEditExamDialog(0);

    else if (this.selectedCreationItem == this.appStrings.stageAttach)
      this.openAddEditAttachmentDialog(0);
  }


  examIntegrationDialog() {
    this.dialog.open(ExamsIntegrationComponent, {
      data: {
        subjectId: this.subjectId,
        unitId: this.selectedUnitId,
      },
      panelClass: 'dialog-radius',
      width: "40%"
    })
      .afterClosed()
      .subscribe(data => {
      });

  }
}
