import { SvgManager } from "app/shared/data/svg-manager";
import { Component, Inject, OnInit } from "@angular/core";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { ActivatedRoute, Router } from "@angular/router";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";

@Component({
  selector: "app-edit-options-dialog",
  templateUrl: "./edit-options-dialog.component.html",
  styleUrls: ["./edit-options-dialog.component.scss"],
})
export class EditOptionsDialogComponent implements OnInit {
  functionId: number;

  svgManager = new SvgManager();
  rowFunctions: IRowFunctionVM[];
  isLoaded: boolean = false;

  constructor(
    private router: Router,
    private ar: ActivatedRoute,
    public authserv: AuthService,
    public dialogRef: MatDialogRef<EditOptionsDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: { courseId: number }
  ) {
    ar.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res.length > 0) {
              this.rowFunctions = res;
            }
            this.isLoaded = true;
          });
      }
    });
  }

  ngOnInit() {}

  navigate(isGeneralData: boolean) {
    this.dialogRef.close();

    // navigate to edit course general data
    if (isGeneralData)
      this.router.navigate(
        [`dashboard/setup/course/edit-course/${this.data.courseId}/0`],
        {
          queryParams: {
            functionId: this.functionId,
          },
        }
      );
    // navigate to edit course structure
    else
      this.router.navigate(
        [`dashboard/setup/course/course-structure/${this.data.courseId}`],
        {
          queryParams: {
            functionId: this.functionId,
          },
        }
      );
  }
}
