import {Component, OnInit, ViewChild} from "@angular/core";
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {MatDialog} from "@angular/material/dialog";
import {MatPaginator} from "@angular/material/paginator";
import {MatSort} from "@angular/material/sort";
import {MatTableDataSource} from "@angular/material/table";
import {ActivatedRoute} from "@angular/router";
import {diftCategoryAddEditVM, giftCategoryViewModel,} from "app/admin/models/admin/gifts";
import {GiftsService} from "app/admin/services/Admin/gifts.service";
import {DeleteDialogComponent} from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import {IRowFunctionVM} from "app/shared/models/general/general";
import {AuthService} from "app/shared/services/auth/auth.service";
import {LanguageService} from "app/shared/services/language.service";
import {MessagesService} from "app/shared/services/messages.service";
import {CustomeValidator} from "app/shared/validators/customeValid.validator";
import {NgxSpinnerService} from "ngx-spinner";
import {
  GiftsCategoryAddComponent
} from "app/admin/pages/setup/gifts-category/gifts-category-add/gifts-category-add.component";

@Component({
  selector: "app-gifts-category-index",
  templateUrl: "./gifts-category-index.component.html",
  styleUrls: ["./gifts-category-index.component.scss"],
})
export class GiftsCategoryIndexComponent implements OnInit {
  form!: FormGroup;
  curPage: number = 1;
  itemsCount: any;
  functionId: number;
  submitted: boolean = false;
  EduCompId: any;
  rowFunctions: IRowFunctionVM[];
  // giftCategory: giftCategoryViewModel[] = [];
  dataSource: MatTableDataSource<giftCategoryViewModel>;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  displayedColumns: string[] = [
    // "#",
    "englishName",
    "arabicName",
    "actions",
  ];

  displayedColumnsSkeleton: string[] = [
    // "#",
    "course.englishName",
    "course.arabicName",
    "Actions",
  ];
  isLoaded: boolean = false;

  constructor(
    private fb: FormBuilder,
    private spinner: NgxSpinnerService,
    private giftsService: GiftsService,
    private route: ActivatedRoute,
    private authserv: AuthService,
    private dialog: MatDialog,
    private toastr: MessagesService,
    public languageService: LanguageService
  ) {
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      // console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res) {
              this.rowFunctions = res;
              // console.log(`RowFunctions for"${this.functionId}": `, res);
            }
          });
      }
    });

    this.EduCompId = this.authserv.getEduCompId();
  }

  get FormCtrls() {
    return this.form.controls;
  }

  ngOnInit() {
    this.form = this.fb.group({
      id: [0],
      englishName: [null, [Validators.required, CustomeValidator.whiteSpace]],
      arabicName: [null, [Validators.required, CustomeValidator.whiteSpace]],
    });

    this.getGiftsCategoryAll(this.curPage);
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }

    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getGiftsCategoryAll(1);
      this.dataSource.paginator = this.paginator;
    });
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    /* if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    } */
  }

  getGiftsCategoryButton(page: number) {
    if (!this.FormCtrls.arabicName.value && !this.FormCtrls.englishName.value) {
      this.toastr.warning("Gifts.enterOneField");
      return;
    } else {
      //  this.getGiftsCategorySearch(page);
      this.dataSource.paginator = this.paginator;
    }
  }

  // getGiftsCategorySearch(page: number) {
  //   let searchData: diftCategorySearchVM = {
  //     englishName: this.FormCtrls?.englishName.value?.trim().toLowerCase(),
  //     arabicName: this.FormCtrls?.arabicName.value?.trim().toLowerCase(),
  //     eduCompId: this.EduCompId,
  //   };
  //   // "" convert to null
  //   Object.keys(searchData).forEach((key, index) => {
  //     if (searchData[key] == "") searchData[key] = null;
  //   });
  //
  //   this.curPage = page;
  //   this.spinner.show();
  //   this.giftsService
  //     .getCategoriesSearch(page, searchData)
  //     .subscribe((res: any) => {
  //       if (res) {
  //         // this.giftCategory = res.giftcategoryList;
  //         // console.log("categories", this.giftCategory);
  //         this.dataSource = new MatTableDataSource(res.giftcategoryList);
  //         this.itemsCount = res.countItems;
  //         this.dataSource.sort = this.sort;
  //       }
  //       this.spinner.hide();
  //     });
  // }

  getGiftsCategoryAll(page: number) {
    this.curPage = page;
    // this.spinner.show();
    this.giftsService
      .getCategoriesAll(this.EduCompId, page)
      .subscribe((res: any) => {
        if (res) {
          // this.giftCategory = res.giftcategoryList;
          // console.log("categories", this.giftCategory);
          this.dataSource = new MatTableDataSource(res.giftcategoryList);
          this.itemsCount = res.countItems;
          this.dataSource.sort = this.sort;
        }
        this.isLoaded = true;
        // this.spinner.hide();
      });
  }

  toEditGiftCategory(giftCategory: giftCategoryViewModel) {
    this.form.patchValue(giftCategory);
  }

  onSubmit() {
    this.submitted = true;
    if (this.form.valid) {
      let giftCategory: diftCategoryAddEditVM = {
        id: this.FormCtrls.id.value,
        eduCompId: this.EduCompId,
        arabicName: this.FormCtrls.arabicName.value,
        englishName: this.FormCtrls.englishName.value,
      };

      this.spinner.show();
      // console.log("form data", this.myForm.value);
      this.giftsService.addEditCategory(giftCategory).subscribe((res: any) => {
        if (res.returnValue == 1) {
          if (giftCategory.id == 0) {
            this.getGiftsCategoryAll(1); // first page after add
            this.dataSource.paginator = this.paginator;
          } else {
            this.getGiftsCategoryAll(this.curPage); // current page after update
          }

          this.toastr.success(
            giftCategory.id == 0
              ? "General.AddSuccessfully"
              : "General.UpdateSuccessfully"
          );

          this.clearForm();
        } else this.toastr.error(res?.returnString);
        this.spinner.hide();
      });
    } else this.toastr.error("General.FormNotValid");
  }

  openDeleteDialog(giftCategory: giftCategoryViewModel): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: this.languageService.isArabic
            ? giftCategory.arabicName ?? giftCategory.englishName
            : giftCategory.englishName ?? giftCategory.arabicName,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.removeGiftCategory(giftCategory);
      });
  }

  removeGiftCategory(giftCategory: giftCategoryViewModel) {
    this.spinner.show();
    this.giftsService
      .RemoveGiftCategory(giftCategory.id)
      .subscribe((response: any) => {
        if (response.returnValue == 2)
          this.toastr.error("Gifts.giftCategoryDeleteError");
        else {
          this.toastr.success("General.DeleteSuccessfully");
          if (this.dataSource.data.length == 1 && this.curPage > 1) {
            // if last item in list: get previous page
            this.getGiftsCategoryAll(this.curPage - 1);
            this.paginator.pageIndex = this.curPage - 1;
          } else {
            // if not last item in list: get current page
            this.getGiftsCategoryAll(this.curPage);
          }
          if (giftCategory.id == this.FormCtrls.id.value) this.clearForm();
        }
        this.spinner.hide();
      });
  }

  resetSearch() {
    this.clearForm();
    this.getGiftsCategoryAll(1);
    this.dataSource.paginator = this.paginator;
  }

  clearForm() {
    this.form.reset();
    this.FormCtrls.id.setValue(0);
    this.submitted = false;
  }

  openAddDialog(giftCategory: giftCategoryViewModel) {
    this.dialog.open(GiftsCategoryAddComponent, {
      data: giftCategory,
      panelClass: "dialog-radius",
      minHeight: "30%"
    }).afterClosed()
      .subscribe((confirm) => {
        console.log(confirm)
        if (confirm) this.getGiftsCategoryAll(this.curPage);
      });
  }
}
