import {HttpClient} from "@angular/common/http";
import {Injectable} from "@angular/core";
import {
  CategorieResponse,
  subCategoriesResponse,
} from "app/admin/models/setup/categories";
import {
  ISubCategoriesResponse,
} from "app/admin/models/setup/categories";
import {AppClientService} from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class CategoriesService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  getCategories() {
    return this.get<CategorieResponse>(
      `SubCategoryCourse/getAllMainCategories`
    );
  }

  getSubCategoriesByMainId(id: number) {
    return this.get<subCategoriesResponse>(
      `SubCategoryCourse/getAllSubCategoriesByMainId/${id}`
    );
  }

  // getCategoriesDropdown(EduCompId: number) {
  //   return this.get<CategorieResponse>(
  //     `SubCategoryCourse/getAllMainCategories/${EduCompId}`
  //   );
  // }

  // deleteCategories(id: number) {
  //   return this.delete(`courseCategory/DeleteCategories/${id}`);
  // }

  // editCategories<T>(model: T, id: number) {
  //   return this.put<T>(`courseCategory/PutCategories/${id}`, model);
  // }

  addCategories<T>(model: T) {
    return this.post<T>(`SubCategoryCourse/AddCategory`, model);
  }

  getAllSubCategoriesByMainId(mainCategoryId: number) {
    return this.get<ISubCategoriesResponse>(
      `SubCategoryCourse/getAllSubCategoriesByMainId/${mainCategoryId}`
    );
  }

  // getAllSubCategoriesByMainIds(mainCategoryIds: number[]) {
  //   return this.post<ISubCategoriesWithMainCategoryResponse>(
  //     `SubCategoryCourse/getAllSubCategoriesByMainIds`,
  //     {
  //       cat_ids: mainCategoryIds,
  //     }
  //   );
  // }


  sendDataToHR(EduComp: number) {
    return this.get(`HRcourseCategory/CopyCategoriesFromEgyToHR/${EduComp}`);
  }

  getDataFromHR(EduComp: number) {
    return this.get(`HRcourseCategory/CopyCategoriesFromHRtoEgy/${EduComp}`);
  }

}
