import {Component, Input, OnInit} from "@angular/core";
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {ActivatedRoute} from "@angular/router";
import {CategorieRequest} from "app/admin/models/setup/categories";
import {CategoriesService} from "app/admin/services/setup/categories.service";
import {UploadFileService} from "app/shared/services/General/upload-file.service";
import {AuthService} from "app/shared/services/auth/auth.service";
import {LanguageService} from "app/shared/services/language.service";
import {MessagesService} from "app/shared/services/messages.service";
import {CustomeValidator} from "app/shared/validators/customeValid.validator";
import {NgxSpinnerService} from "ngx-spinner";
import {FileModel} from "app/shared/models/general/general";

@Component({
  selector: "app-add-category",
  templateUrl: "./add-category.component.html",
  styleUrls: ["./add-category.component.scss"],
})
export class AddCategoryComponent implements OnInit {
  form!: FormGroup;
  submitted: boolean = false;
  EduCompId: number;
  @Input() showAddOnly: boolean = false;
  addSubCat: boolean = false;
  SubCat_Names: string[] = [];
  selectedIcon;
  removeImage: number = 0;
  functionId: number = 0;

  constructor(
    private fb: FormBuilder,
    private _serviceCategories: CategoriesService,
    private toastr: MessagesService,
    private spinner: NgxSpinnerService,
    public fileService: UploadFileService,
    private route: ActivatedRoute,
    public authService: AuthService,
    public languageService: LanguageService
  ) {
    this.EduCompId = Number(authService.getEduCompId());
    //  this.showAddOnly = data?.showAddOnly ?? false;
    route.queryParamMap.subscribe(param => {
      this.functionId = Number(param.get('functionId'))
    })
  }

  ngOnInit(): void {
    // this.getCategories(this.curPage);
    this.form = this.fb.group({
      id: [0],
      Cat_Name: [null, [Validators.required, CustomeValidator.whiteSpace]],
      SubCat_Name: [null],
      File: [null],
    });
  }

  get FormCtrls() {
    return this.form.controls;
  }

  addSubCategory() {
    this.addSubCat = true;
  }

  saveNewSubCategory() {
    if (this.form.value.SubCat_Name) {
      let Name = this.form.value.SubCat_Name;
      this.SubCat_Names.push(Name);
      this.addSubCat = false;
      this.FormCtrls.SubCat_Name.setValue(null);
    }
  }

  RemoveFile() {
    this.removeImage = 1;
    if (this.FormCtrls.File) this.FormCtrls.File.setValue(null);
  }

  Create() {
    this.submitted = true;
    if (this.form.valid) {
      const model: CategorieRequest = {
        Cat_Name: this.form.value.Cat_Name,
        File: this.form.value.File,
        Sub_Names: this.SubCat_Names,
      };

      this.spinner.show();
      this._serviceCategories.addCategories(model).subscribe((res: any) => {
        if (res.returnValue == 1) {
          this.clearForm();
          this.toastr.success("General.AddSuccessfully");
        } else this.toastr.error(res.returnString);
        this.spinner.hide();
      });
    } else this.toastr.error("General.FormNotValid");
  }

  clearForm() {
    this.form.reset();
    this.SubCat_Names = [];
    this.submitted = false;
    this.addSubCat = false;
  }

  onSelectIcon(event) {
    const imgSrc = event.target.src;
    this.fileService
      .convertImgToBase64(imgSrc)
      .then((file: FileModel) => {
        this.FormCtrls.File.setValue(file);
      })
      .catch((error) => {
        console.error("error on select icon:", error);
      });
  }

  iconsList = [
    {
      id: 0,
      path: "assets/gif/category-1.gif",
    },
    {
      id: 1,
      path: "assets/gif/category-2.gif",
    },
    {
      id: 2,
      path: "assets/gif/category-3.gif",
    },
    {
      id: 3,
      path: "assets/gif/category-4.gif",
    },
    {
      id: 4,
      path: "assets/gif/category-5.gif",
    },
    {
      id: 5,
      path: "assets/gif/category-6.gif",
    },
  ];
}

