import {Component, OnInit} from "@angular/core";
import {TranslateService} from "@ngx-translate/core";
import {CategorieResponse, CategoriesModel,} from "app/admin/models/setup/categories";
import {CategoriesService} from "app/admin/services/setup/categories.service";
import {UploadFileService} from "app/shared/services/General/upload-file.service";
import {AuthService} from "app/shared/services/auth/auth.service";
import {NgxSpinnerService} from "ngx-spinner";
import {ActivatedRoute} from "@angular/router";
import {defaultRes} from "app/shared/models/general/general";
import {ResMessage} from "app/shared/enums/res-message";
import {MessagesService} from "app/shared/services/messages.service";

@Component({
  selector: "app-categorie",
  templateUrl: "./categorie.component.html",
  styleUrls: ["./categorie.component.scss"],
})
export class CategorieComponent implements OnInit {
  categories: CategoriesModel[];
  curLang: string;
  functionId: number;
  EduCompId: number;
  isLoaded: boolean = false;

  constructor(
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    public authService: AuthService,
    public fileService: UploadFileService,
    public categorieservice: CategoriesService,
    private route: ActivatedRoute,
    private messagesService: MessagesService,
  ) {
    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });

    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
    });
  }

  ngOnInit(): void {
    this.EduCompId = Number(this.authService.getEduCompId());
    if (this.authService.isAdmin()) this.geAll();
  }

  ngAfterViewInit() {
    this.authService.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.geAll();
    });
  }


  geAll() {
    // this.spinner.show();
    this.categorieservice
      .getCategories()
      .subscribe((res: CategorieResponse) => {
        this.categories = res.MainCategories;
        this.isLoaded = true;
        //  this.spinner.hide();
      });
  }


  getDataFromHR() {
    this.spinner.show();
    this.categorieservice.getDataFromHR(this.EduCompId)
      .subscribe((res: defaultRes) => {
        this.spinner.hide();
        if (res.returnValue == ResMessage.success) {
          this.geAll();
          this.messagesService.success('copyHrSuccess');
        }
      });
  }

  sendDataToHR() {
    this.spinner.show();
    this.categorieservice.sendDataToHR(this.EduCompId)
      .subscribe((res: defaultRes) => {
        this.spinner.hide();
        if (res.returnValue == ResMessage.success) {
          // this.geAll();
          this.messagesService.success('sendHrSuccess');
        }
      });
  }

}
