import {Component, Inject, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {NgxSpinnerService} from "ngx-spinner";
import {AuthService} from "app/shared/services/auth/auth.service";
import {MAT_DIALOG_DATA, MatDialogRef} from "@angular/material/dialog";
import {CopyExamsToHRRequest, ExamsService} from "app/admin/services/Admin/exams.service";
import {forkJoin} from "rxjs";
import {LanguageService} from "app/shared/services/language.service";
import {ResMessage} from "app/shared/enums/res-message";
import {defaultRes} from "app/shared/models/general/general";
import {MessagesService} from "app/shared/services/messages.service";
import {defaultInputAppearance, InputAppearance} from "app/shared/enums/mat-theme.enum";

interface EgyExamsModel {
  Egy_ar_exam: string;
  Egy_en_exam: string;
  exam_id: number;
}

interface EgyimplementationModel {
  Imp_From_Date: string;
  Imp_ID: number;
  Imp_To_Date: string;

}


@Component({
  selector: 'app-exams-integration',
  templateUrl: './exams-integration.component.html',
  styleUrls: ['./exams-integration.component.scss']
})

export class ExamsIntegrationComponent implements OnInit {
  subjectId: any;
  form!: FormGroup;
  EduCompId: any;
  exams: EgyExamsModel[];
  implementationList: EgyimplementationModel[];
  submitted: boolean = false
  appearance: InputAppearance = defaultInputAppearance;

  get impIdCtrl() {
    return this.form.get("impId");
  }

  get examIdsCtrl() {
    return this.form.get("examIds");
  }

  constructor(
    private fb: FormBuilder,
    private spinner: NgxSpinnerService,
    private examsService: ExamsService,
    public authserv: AuthService,
    public langService: LanguageService,
    private toastr: MessagesService,
    @Inject(MAT_DIALOG_DATA) public data: { subjectId: number, unitId: number },
    private dialogRef: MatDialogRef<ExamsIntegrationComponent>
  ) {
    this.subjectId = data.subjectId;
    this.EduCompId = this.authserv.getEduCompId();
    spinner.show()
    forkJoin([
      this.examsService.getImplemenationsByCourseID(this.subjectId, this.EduCompId),
      this.examsService.getExamByCourseId(this.subjectId)
    ]).subscribe((res: any) => {
      spinner.hide()
      let implementation: { returnValue: number, ImplementationList: EgyimplementationModel[] } = res[0];
      let exam: { returnValue: number, EgyExams: EgyExamsModel[] } = res[1];

      if (implementation?.returnValue == ResMessage.success) {
        this.implementationList = implementation?.ImplementationList
      }

      if (exam?.returnValue == ResMessage.success) {
        this.exams = exam?.EgyExams
      }
    })
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      impId: [null, Validators.required],
      examIds: [null, Validators.required],
      eduCompId: [this.EduCompId, Validators.required],
    });
  }


  copyExamsToHR() {
    this.submitted = true;
    if (this.form.valid) {
      this.spinner.show();
      let body: CopyExamsToHRRequest = {
        EduComp: this.EduCompId,
        impID: this.impIdCtrl.value,
        ExamsIDs: this.examIdsCtrl.value
      };
      this.examsService.copyExamsToHR(body).subscribe((res: defaultRes) => {
        if (res.returnValue == ResMessage.success) {
          this.toastr.success('sendHrSuccess');
          this.spinner.hide();
          this.dialogRef.close();
        }
      })
    } else this.toastr.error("General.FormNotValid");

  }
}
