  constructor(
    private fb: FormBuilder,
    private giftsService: GiftsService,
    private dialog: MatDialog,
    private toastr: MessagesService,
    private authserv: AuthService,
    public languageService: LanguageService,
    public deviceService: DeviceDetectorService,
    private route: ActivatedRoute
  ) {
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      //    console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res) {
              this.rowFunctions = res;
              //  console.log(`RowFunctions for"${this.functionId}": `, res);
            }
          });
      }
    });

    this.EduCompId = this.authserv.getEduCompId();
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  ngOnInit() {
    this.myForm = this.fb.group({
      // page: [1],
      englishName: [null],
      arabicName: [null],
      giftsCategoryId: [null],
      points: [null],
      // EduCompId: [this.EduCompId],
    });

    // this.getAllGiftCategory();
    this.getGiftsAll();
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.startSortingTable();
    }

    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      //  this.getAllGiftCategory();
      this.getGiftsAll();
      this.dataSource.paginator = this.paginator;
    });
  }

  startSortingTable() {
    this.dataSource.sortingDataAccessor = (item, property) => {
      switch (property) {
        case "giftCategory":
          return this.languageService.isArabic
            ? item.arbGiftCategory ?? item.engGiftCategory
            : item.engGiftCategory ?? item.arbGiftCategory;
        default:
          return item[property];
      }
    };
    this.dataSource.sort = this.sort;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    /* if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    } */
  }

