  getGiftsAll() {
    // this.curPage = page;
    // this.spinner.show();
    this.giftsService
      .getAllGifts(this.EduCompId)
      .subscribe((res: IGiftGalleryResponse) => {
        if (res) {
          // this.gifts = res.giftList;
          // console.log("gifts", res.giftList);
          // this.dataSource = new MatTableDataSource(res.giftList);
          this.gifts = res.giftList;
          // this.itemsCount = res.countItems;
          //  this.startSortingTable();
        }
        this.isLoaded = true;
        // this.spinner.hide();
      });
  }

