  openAddDialog(gift: IGiftGalleryModel) {
    this.dialog
      .open(GiftsAddComponent, {
        data: gift,
        panelClass: "dialog-radius",
        minHeight: "30%",
        width: "50%",
      })
      .afterClosed()
      .subscribe((confirm) => {
        console.log(confirm);
        if (confirm) this.getGiftsAll();
      });
  }

  resetSearch() {
    this.myForm.reset();
    this.getGiftsAll();
    this.dataSource.paginator = this.paginator;
  }

