import {Component, OnInit} from '@angular/core';
import {IPartVM} from "app/admin/models/admin/educations";
import {MessagesService} from "app/shared/services/messages.service";
import {NgxSpinnerService} from "ngx-spinner";
import {SubjectMaterialsService} from "app/admin/services/Admin/subjectMaterials.service";
import {ActivatedRoute} from "@angular/router";
import {DeleteDialogComponent} from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import {MatDialog} from "@angular/material/dialog";
import {SvgManager} from "app/shared/data/svg-manager";
import {
  AddMaterialPartsComponent
} from "app/admin/pages/educational-setup/EducationalSubject/add-materials/add-material-parts/add-material-parts.component";

@Component({
  selector: 'app-mat-parts-index',
  templateUrl: './mat-parts-index.component.html',
  styleUrls: ['./mat-parts-index.component.scss']
})
export class MatPartsIndexComponent implements OnInit {
  materialId: any;
  functionId: any;
  subjectId: any;
  parts: IPartVM[] = [];
  svgManager = new SvgManager();
  isLoaded: boolean = false

  constructor(private toastr: MessagesService,
              private SubjectMaterialsService: SubjectMaterialsService,
              private spinner: NgxSpinnerService,
              private route: ActivatedRoute,
              private dialog: MatDialog
  ) {
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      this.materialId = Number(params.get("materialId"));
      this.subjectId = Number(params.get("subjectId"));
    });
    if (this.materialId > 0) {
      this.getMaterialParts();
    }
  }

  ngOnInit(): void {
  }

  getMaterialParts() {
    this.isLoaded = false;
    this.SubjectMaterialsService.getMaterialParts(this.materialId).subscribe(
      (res: any) => {
        if (res.returnValue == 1) {
          //  console.log("Parts", res);
          this.parts = res.MaterialParts;
          this.isLoaded = true;
        }
      }
    );
  }

  deletePart(part: IPartVM) {
    this.spinner.show();
    this.SubjectMaterialsService.deletePart(part.Id).subscribe((res: any) => {
      if (res.returnValue == 1) {
        let i = this.parts.indexOf(part);
        this.parts.splice(i, 1);
        this.toastr.success("General.DeleteSuccessfully");
        this.spinner.hide();
      }
    });
  }

  openDeleateDialog(part: IPartVM): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: part.Name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deletePart(part);
      });
  }


  openCreationDialog(part: IPartVM) {
    this.dialog
      .open(AddMaterialPartsComponent, {
        data: part ?? {materialId: this.materialId},
        width: "500px"
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.getMaterialParts();
      });
  }
}
