import { Component, OnInit, ViewChild } from "@angular/core";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import {
  IsearchModel,
  RequestData,
} from "app/admin/models/admin/course-request";
import { CourseAndTeachingRequestService } from "app/admin/services/Admin/course-teaching-request.service";
import { AppAssets } from "app/shared/data/app-assets";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { AuthService } from "app/shared/services/auth/auth.service";
import { LanguageService } from "app/shared/services/language.service";
import { MessagesService } from "app/shared/services/messages.service";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-employee-requestes",
  templateUrl: "./course-requestes.component.html",
  styleUrls: ["./course-requestes.component.scss"],
})
export class CourseRequestComponent implements OnInit {
  totalItems: number = 0;
  // myForm: FormGroup;
  EduCompId: any;

  // approveList: RadioButtonModel[] = [
  //   {
  //     label: "labels.Approve",
  //     value: true,
  //   },
  //   {
  //     label: "labels.NotApprove",
  //     value: false,
  //   },
  // ];

  displayedColumns: string[] = [
    // "#",
    "employee_name",
    "courseName",
    "reasonOfRequest",
    "numberOfDays",
    "status",
    // "actions",
  ];
  dataSource: MatTableDataSource<RequestData>;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  functionId: number;

  displayedColumnsSkeleton: string[] = [
    "labels.Employee",
    "course.course",
    "labels.Reason",
    "labels.period",
    "Actions",
  ];
  isLoaded: boolean = false;

  constructor(
    // private fb: FormBuilder,
    private courseAndTeachingRequestService: CourseAndTeachingRequestService,
    private authserv: AuthService,
    private tostar: MessagesService,
    private spinner: NgxSpinnerService,
    private languageService: LanguageService,
    private ar: ActivatedRoute,
    public fileService: UploadFileService,
    public appAssets: AppAssets
  ) {
    ar.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
    });

    this.EduCompId = this.authserv.getEduCompId();
  }

  ngOnInit(): void {
    // this.myForm = this.fb.group({
    //   mobile: [null],
    //   code: [null],
    //   email: [null],
    //   department: [null],
    //   jobTitle: [null],
    //   course: [null],
    //   approve: [],
    // });

    this.getAllRequests(1);
  }

  // get FormCtrls() {
  //   return this.myForm.controls;
  // }

  // applyFilter(event: Event) {
  //   const filterValue = (event.target as HTMLInputElement).value;
  //   this.dataSource.filter = filterValue.trim().toLowerCase();
  // }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.startSortingTable();
    }

    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getAllRequests(1);
      this.dataSource.paginator = this.paginator;
    });
  }

  getAllRequests(page: number) {
    this.isLoaded = false;
    let data: IsearchModel = {
      page: page,
      EduCompId: this.EduCompId,
      mobile: /* this.FormCtrls.mobile.value?.trim() */ null,
      code: /* this.FormCtrls.code.value?.trim().toLowerCase() */ null,
      account_email:
        /* this.FormCtrls.email.value?.trim().toLowerCase() */ null,
      approvement: /* this.FormCtrls.approve.value */ null,
      courseId: /* this.FormCtrls.course.value */ null,
    };

    // this.spinner.show();
    this.courseAndTeachingRequestService
      .getAllCourseRequests(data)
      .subscribe((res) => {
        if (res) {
          this.dataSource = new MatTableDataSource(res.Requests);
          this.totalItems = res.countItems;
          this.startSortingTable();
        }
        this.isLoaded = true;
        // this.spinner.hide();
      });
  }

  approveCourseRequest(request: RequestData, status: boolean) {
    this.spinner.show();
    this.courseAndTeachingRequestService
      .ApproveCourseRequest(request.id, status)
      .subscribe((res: any) => {
        if (res.returnValue == 1) {
          let i = this.dataSource.data.findIndex((ele) => ele == request);
          this.dataSource.data[i].status = status;
          this.tostar.success("General.UpdateSuccessfully");
        } else this.tostar.error(res?.returnString);
        this.spinner.hide();
      });
  }

  // resetSearch() {
  //   this.myForm.reset();
  //   this.getAllRequests(1);
  //   this.dataSource.paginator = this.paginator;
  // }

  startSortingTable() {
    // custom sort
    this.dataSource.sortingDataAccessor = (item, property) => {
      switch (property) {
        case "courseName":
          return this.languageService.isArabic
            ? item.course_ar_name ?? item.course_en_name
            : item.course_en_name ?? item.course_ar_name;
        case "employee_name":
          return item.employee_first_name;
        default:
          return item[property];
      }
    };
    this.dataSource.sort = this.sort;
  }
}
