import { Component, OnInit } from "@angular/core";
import {
  IDiplomaAddEditVM,
  IDiplomaDetailsModel,
  IDiplomaEditRequest,
  IDiplomaGetVM,
} from "app/admin/models/admin/setup/course";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { CourseService } from "app/admin/services/Admin/setup/course.service";
import { AuthService } from "app/shared/services/auth/auth.service";
import { NgxSpinnerService } from "ngx-spinner";
import { MessagesService } from "app/shared/services/messages.service";
import { ActivatedRoute } from "@angular/router";
import {
  InputAppearance,
  defaultInputAppearance,
} from "app/shared/enums/mat-theme.enum";
import {
  CategoriesModel,
  subCategoriesModel,
} from "app/admin/models/setup/categories";
import { CategoriesService } from "app/admin/services/setup/categories.service";
import { OriginalFileModel } from "app/shared/models/general/general";

@Component({
  selector: "app-diploma-index",
  templateUrl: "./add-diploma.component.html",
  styleUrls: ["./add-diploma.component.scss"],
})
export class AddDiplomaComponent implements OnInit {
  DiplomaId: number = 0;
  myForm!: FormGroup;
  submitted: boolean = false;
  addOutcome: boolean = false;
  functionId: number;
  EduCompId: number;
  mainCategories: CategoriesModel[];
  subCategories: subCategoriesModel[];
  dipOutcomes: string[] = [];
  appearance: InputAppearance = defaultInputAppearance;
  stepIndex: number = 0;
  backBtnUrl: string;
  Diploma: IDiplomaDetailsModel;
  savedFile: OriginalFileModel;
  constructor(
    private authService: AuthService,
    private courseServ: CourseService,
    private categoriesService: CategoriesService,
    private fb: FormBuilder,
    private ar: ActivatedRoute,
    private spinner: NgxSpinnerService,
    private msg: MessagesService,
    route: ActivatedRoute
  ) {
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
    });
    route.paramMap.subscribe((params) => {
      this.DiplomaId = Number(params.get("id"));
      if (this.DiplomaId > 0) this.getDiplomaById();
    });
  }

  onDeleteImg() {
    this.FormCtrls.diploma_attach?.setValue(null);
  }

  ngOnInit(): void {
    this.EduCompId = Number(this.authService.getEduCompId());

    this.myForm = this.fb.group({
      ar_name: [null, [Validators.required]],
      en_name: [null, [Validators.required]],
      eduCompId: [null],
      promoLink: [null, [Validators.required]],
      diplomaDescreption: [null, [Validators.required]],
      diploma_attach: [null, [Validators.required]],
      subCategoryIds: [null, [Validators.required]], // [16, 17]
      category_ids: [null],
      learningOutcomeName: [null],
    });
    this.getAllCategories();
  }
  addoutcomes() {
    this.addOutcome = true;
  }
  saveNewOutcomes() {
    if (this.FormCtrls.learningOutcomeName.value) {
      let Name = this.FormCtrls.learningOutcomeName.value;
      this.dipOutcomes.push(Name);
      this.addOutcome = false;
      this.FormCtrls.learningOutcomeName.setValue(null);
    }
  }
  getAllCategories() {
    this.categoriesService.getCategories().subscribe((res) => {
      if (res) this.mainCategories = res.MainCategories;
    });
  }
  getDiplomaById() {
    this.courseServ
      .getDiplomaById<IDiplomaDetailsModel>(this.DiplomaId)
      .subscribe((res) => {
        if (res) {
          this.Diploma = res;
          this.myForm.patchValue(this.Diploma);
          this.FormCtrls.ar_name?.setValue(this.Diploma.diploma_ar_name);
          this.FormCtrls.en_name?.setValue(this.Diploma.diploma_en_name);
          this.FormCtrls.diploma_attach?.setValue(this.Diploma.attach_path);
          this.FormCtrls.promoLink?.setValue(this.Diploma.promo_link);
          this.FormCtrls.diplomaDescreption?.setValue(this.Diploma.description);
          // this.dipOutcomes?.concat(this.Diploma.learning_outcomes);
          this.dipOutcomes = this.Diploma.learning_outcomes;
          let mainCatIdVar = this.Diploma.diploma_category[0].mainCatId;
          this.FormCtrls.category_ids?.setValue(mainCatIdVar);
          this.getAllSubCategories(mainCatIdVar);
          let selectedSubCatIds: number[] = []; // [16, 17, 18]
          res.diploma_category.forEach((e) => {
            selectedSubCatIds.push(e.id);
          });
          this.FormCtrls.subCategoryIds.setValue(selectedSubCatIds);
          // this.categoriesService
          //   .getSubCategoriesByMainId(mainCatIdVar)
          //   .subscribe((res) => {
          //     if (res) this.subCategories = res.SubCategories;
          //     // this.spinner.hide();
          //   });
        }
      });
  }
  onChangeMainCategory() {
    let mainCategoryId = this.FormCtrls.category_ids.value;
    // console.log(mainCategoryId);
    this.FormCtrls.subCategoryIds.setValue(null);
    if (mainCategoryId > 0) {
      this.getAllSubCategories(mainCategoryId);
      // console.log(this.FormCtrls.subCategoryIds);
      this.FormCtrls.subCategoryIds.enable();
    } else {
      this.FormCtrls.subCategoryIds.disable();
    }
  }
  getAllSubCategories(mainCategoryId: number) {
    // this.spinner.show();
    this.categoriesService
      .getSubCategoriesByMainId(mainCategoryId)
      .subscribe((res) => {
        if (res) this.subCategories = res.SubCategories;
        // this.spinner.hide();
      });
  }
  onSubmit() {
    this.submitted = true;

    if (this.myForm.valid) {
      this.spinner.show();
      let Diploma: IDiplomaAddEditVM = {
        ar_name: this.FormCtrls.ar_name.value,
        en_name: this.FormCtrls.en_name.value,
        diploma_attach: this.FormCtrls.diploma_attach.value,
        promoLink: this.FormCtrls.promoLink.value,
        LearningOutComes: this.dipOutcomes,
        subCategoryIds: this.FormCtrls.subCategoryIds.value,
        diplomaDescreption: this.FormCtrls.diplomaDescreption.value,
        eduCompId: this.EduCompId,
      };
      // add or edit
      if (this.DiplomaId == 0) this.addDiploma(Diploma);
      else this.updateDiploma(Diploma);
    } else this.msg.error("General.FormNotValid");
  }

  addDiploma(Diploma: IDiplomaAddEditVM) {
    this.courseServ
      .addDiploma<IDiplomaAddEditVM, any>(Diploma)
      .subscribe((res) => {
        if (res?.returnValue == 1) {
          this.msg.success("General.AddSuccessfully");
          this.clearForm();
        } else {
          this.msg.error(res?.returnString);
        }
        this.spinner.hide();
      });
  }

  updateDiploma(Diploma: IDiplomaAddEditVM) {
    this.courseServ
      .updateDiploma<IDiplomaAddEditVM, any>(this.DiplomaId, Diploma)
      .subscribe((res) => {
        if (res?.returnValue == 1) {
          this.msg.success("General.UpdateSuccessfully");
          // this.clearForm();
        } else {
          this.msg.error(res?.returnString);
        }
        this.spinner.hide();
      });
  }

  toEditDiploma(Diploma: IDiplomaDetailsModel) {
    this.DiplomaId = Diploma.id;
    this.myForm.patchValue(Diploma);
    // this.FormCtrls.ar_name?.setValue(this.Diploma.diploma_ar_name);
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  clearForm() {
    this.myForm.reset();
    this.submitted = false;
    this.DiplomaId = 0;
    this.dipOutcomes = null;
  }
}
